import * as React from 'react'
import Page from '../components/Page'
import { NextContext } from 'next'
import Post from '../components/Post'
import { Posts } from '../data/PostMenu'
// import Router from 'next/router'
import dynamic from 'next/dynamic'
import { DemoProps } from '../components/Demo';

class DocPage extends React.Component<{ title: keyof typeof Posts, content: string, demos: any }> {
  static getInitialProps = async ({ query }: NextContext<{ title: string }>) => {
    let content: any
    let demos: any
    try {
      content = await import(`../markdowns/${query.title}.md`)
      content = content.default
    } catch{
      console.log('加载失败')
    }
    try {
      demos = dynamic(() => import('../components/Demo'))

      // demos = await import(`../demos/${query.title}`)
      // console.log(demos.default)
      // demos = demos.default
    } catch{
      // demos = null
    }
    return { title: query.title, content, demos }
  }
  // static defaultProps = {
  //   title: 'button',
  //   cotent: '123',
  //   demos: <p>123</p>
  // }
  render() {
    const { title, content, demos } = this.props
    console.log('Component:', demos)
    return (<Page title={`${Posts[title].subTitle ? Posts[title].subTitle + ' ' : ''}${Posts[title].title} - Semon UI`}>
      <Post>{content}</Post>
      <Post>
        <h2>代码演示</h2>
        {/* <Demos title='' description=''></Demos> */}
      </Post>
    </Page>
    )
  }
}

export default DocPage
